import { configAction } from "./action";

const initialState = {
  config: {
    base_url: "",
    app_name: "",
    app_title: "",
  },
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case configAction.type: {
      return { ...state, config: action.config };
    }
    default:
      return { ...state };
  }
};
export default reducer;
