import { notification } from "antd";

export const SuccesNotification = (
  message = "Success",
  desc = "Data has been updated!"
) => {
  notification.info({
    message: message,
    description: desc,
  });
};

export const ErrorNotification = (
  message = "Error",
  desc = "Data has been deleted!"
) => {
  notification.error({
    message: message,
    description: desc,
  });
};
