import { assetStatusListAction } from "./action";

const initialState = {
  assetStatusList: [],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case assetStatusListAction.type: {
      return { ...state, assetStatusList: action.assetStatusList };
    }
    default:
      return { ...state };
  }
};
export default reducer;
