import { branchListAction } from "./action";

const initialState = {
  branchList: [],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case branchListAction.type: {
      return { ...state, branchList: action.branchList };
    }
    default:
      return { ...state };
  }
};
export default reducer;
