import "../styles/globals.css";
import "antd/dist/reset.css";
import React, { useEffect, useState } from "react";
import NextNProgress from "nextjs-progressbar";
import { useRouter } from "next/router";
import { CLayout, CLoadingSpinner, CServerError } from "@components";
import { wrapper } from "../redux/store";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import CApi from "@api";
import { updateConfig, updateTitle } from "@action";

export const config = {
  unstable_runtimeJS: false,
};

const AssetApp = ({ Component, pageProps }) => {
  const [isMounted, setIsMounted] = useState(false);
  const router = useRouter();

  const { config } = useSelector((state) => state.config);
  if (isMounted && config.base_url != "") {
    CApi.defaults.baseURL = config.base_url;
  }
  const dispatch = useDispatch();

  let path = router.pathname;
  const route = {
    "": true,
    asset: true,
    audit: true,
    master: true,
    report: true,
    developer: true,
    setup: true,
    profile: true,
    "mobile-app": true,
  };
  let toArray = path.split("/")[1];

  useEffect(() => {
    setIsMounted(true);
    getConfig();
  }, []);

  const getConfig = () => {
    const url = location.origin;
    axios({
      url: url + "/api/config",
      method: "get",
    }).then((res) => {
      dispatch(updateConfig(res.data));
      dispatch(updateTitle(res.data.app_title));
    });
  };

  if (!isMounted || config.base_url == "") return <CLoadingSpinner />;

  return toArray in route ? (
    <CServerError>
      <CLayout>
        <NextNProgress
          height={5}
          color="#1890FF"
          options={{ showSpinner: false }}
        />
        <Component {...pageProps} />
      </CLayout>
    </CServerError>
  ) : (
    <CServerError>
      <NextNProgress
        height={5}
        color="#1890FF"
        options={{ showSpinner: false }}
      />
      <Component {...pageProps} />
    </CServerError>
  );
};

export default wrapper.withRedux(AssetApp);
