import { menuAction } from "./action";

const initialState = {
  menus: [
    {
      label: "Dashboard",
      icon: "pie-chart",
      path: "/",
    },
  ],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case menuAction.type: {
      return { ...state, menus: action.menu };
    }
    default:
      return { ...state };
  }
};
export default reducer;
