import { assetTypeListAction } from "./action";

const initialState = {
  assetTypeList: [],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case assetTypeListAction.type: {
      return { ...state, assetTypeList: action.data };
    }
    default:
      return { ...state };
  }
};
export default reducer;
