import { departmentListAction } from "./action";

const initialState = {
  departmentList: [],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case departmentListAction.type: {
      return { ...state, departmentList: action.departmentList };
    }
    default:
      return { ...state };
  }
};
export default reducer;
